import React, { useEffect, useRef, useState } from 'react';
import { useHistory } from 'react-router-dom';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader';
import '../styles/western-theme.css';
import hitSound1 from '../sounds/hit1.mp3';
import hitSound2 from '../sounds/hit2.mp3';
import hitSound3 from '../sounds/hit3.mp3';
import hitSound4 from '../sounds/hit4.mp3';
import hitSound5 from '../sounds/hit5.mp3';
import hitSound6 from '../sounds/hit6.mp3';
import hitSound7 from '../sounds/hit7.mp3';
import hitSound8 from '../sounds/hit8.mp3';
import hitSound9 from '../sounds/hit9.mp3';
import hitSound10 from '../sounds/hit10.mp3';
import hitSound11 from '../sounds/hit11.mp3';
import hitSound12 from '../sounds/hit12.mp3';
import skullIcon from '../images/skull.png';
import ParticleSystem from './ParticleSystem';

const hitSounds = [
  hitSound1, hitSound2, hitSound3, hitSound4, hitSound5, hitSound6,
  hitSound7, hitSound8, hitSound9, hitSound10, hitSound11, hitSound12
];

const Wasteland = ({ volume }) => {
  const mountRef = useRef(null);
  const cameraRef = useRef(null);
  const [remainingBandits, setRemainingBandits] = useState(5);
  const banditsRef = useRef([]);
  const hitboxesRef = useRef([]);
  const skullIconsRef = useRef([]);
  const hitBanditsRef = useRef(new Set());
  const history = useHistory();
  const particleSystemRef = useRef(null);
  const sceneRef = useRef(new THREE.Scene());
  const mixersRef = useRef([]);
  const clockRef = useRef(new THREE.Clock());

  useEffect(() => {
    const scene = sceneRef.current;
    const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
    cameraRef.current = camera;
    const renderer = new THREE.WebGLRenderer({ antialias: true });
    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.shadowMap.enabled = true;
    renderer.shadowMap.type = THREE.PCFSoftShadowMap;
    mountRef.current.appendChild(renderer.domElement);

    const controls = new OrbitControls(camera, renderer.domElement);
    controls.enableDamping = true;
    controls.dampingFactor = 0.25;
    controls.screenSpacePanning = false;
    controls.enableZoom = true;
    controls.enablePan = true;

    // Floor setup
    const textureLoader = new THREE.TextureLoader();
    const floorTexture = textureLoader.load('https://raw.githubusercontent.com/wellb3tz/theQuickandtheDead/main/frontend/media/soil3.png');
    const extendedFloorGeometry = new THREE.PlaneGeometry(100, 100, 32, 32);
    const extendedFloorMaterial = new THREE.ShaderMaterial({
      uniforms: {
        center: { value: new THREE.Vector2(0, 0) },
        radius: { value: 50.0 },
        floorTexture: { value: floorTexture }
      },
      vertexShader: `
        varying vec2 vUv;
        varying float vDistance;
        void main() {
          vUv = uv;
          vec4 worldPosition = modelMatrix * vec4(position, 1.0);
          vDistance = length(worldPosition.xz);
          gl_Position = projectionMatrix * viewMatrix * worldPosition;
        }
      `,
      fragmentShader: `
        uniform float radius;
        uniform sampler2D floorTexture;
        varying vec2 vUv;
        varying float vDistance;
        void main() {
          vec4 color = texture2D(floorTexture, vUv);
          float alpha = 1.0 - smoothstep(radius * 0.5, radius, vDistance);
          gl_FragColor = vec4(color.rgb, color.a * alpha);
        }
      `,
      transparent: true,
      side: THREE.DoubleSide
    });
    const extendedFloorMesh = new THREE.Mesh(extendedFloorGeometry, extendedFloorMaterial);
    extendedFloorMesh.rotation.x = -Math.PI / 2;
    extendedFloorMesh.receiveShadow = true;
    scene.add(extendedFloorMesh);

    // Skybox
    textureLoader.load('https://raw.githubusercontent.com/wellb3tz/theQuickandtheDead/main/frontend/media/skybox_desert1.png', (skyboxTexture) => {
      const rt = new THREE.WebGLCubeRenderTarget(skyboxTexture.image.height);
      rt.fromEquirectangularTexture(renderer, skyboxTexture);
      scene.background = rt.texture;
    });

    const loader = new FBXLoader();

    // Load bandit model with error handling
    loader.load('https://raw.githubusercontent.com/wellb3tz/theQuickandtheDead/main/frontend/media/dyingfront.fbx', 
      (fbx) => {
        console.log('Model loaded. Animations:', fbx.animations);
        
        // Use first animation (Mixamo exports often have single animation)
        const deathClip = fbx.animations[0];
        
        if (!deathClip) {
          console.error('No animations found in model');
          return;
        }
    
        // Optional: Rename the clip for debugging
        deathClip.name = 'DeathAnimation';
        console.log('Using animation clip:', deathClip.name);
    
        // Create scaled model template
        const modelTemplate = fbx.clone();
        modelTemplate.scale.set(2.0, 2.0, 2.0); // Increased scale for visibility
    
        // Debug material for better visibility
        const debugMaterial = new THREE.MeshNormalMaterial();
    
        for (let i = 0; i < 5; i++) {
          const bandit = modelTemplate.clone();
          bandit.position.set(
            Math.random() * 20 - 10,
            0,
            Math.random() * 20 - 10
          );
    
          // Animation setup
          const mixer = new THREE.AnimationMixer(bandit);
          const action = mixer.clipAction(deathClip);
          action.setLoop(THREE.LoopOnce);
          action.clampWhenFinished = true;
          
          mixersRef.current.push({ 
            mixer, 
            action, 
            isDead: false 
          });
    
          // Apply materials and shadows
          bandit.traverse(child => {
            if (child.isMesh) {
              child.material = debugMaterial;
              child.castShadow = true;
              child.receiveShadow = true;
            }
          });
    
          scene.add(bandit);
          banditsRef.current.push(bandit);
    
          // Hitbox setup (visible for debugging)
          const hitboxGeometry = new THREE.BoxGeometry(2, 3, 1);
          const hitboxMaterial = new THREE.MeshBasicMaterial({ 
            color: 0xff0000, 
            wireframe: true,
            transparent: true,
            opacity: 0.3
          });
          const hitbox = new THREE.Mesh(hitboxGeometry, hitboxMaterial);
          hitbox.position.copy(bandit.position);
          scene.add(hitbox);
          hitboxesRef.current.push(hitbox);
        }
      },
      undefined,
      (error) => {
        console.error('FBX loading failed:', error);
      }
    );

    // Lighting
    const light = new THREE.DirectionalLight(0xffffff, 1);
    light.position.set(10, 10, 10);
    light.castShadow = true;
    light.shadow.mapSize.width = 1024;
    light.shadow.mapSize.height = 1024;
    scene.add(light);

    camera.position.set(0, 5, 15);
    controls.target.set(0, 0, 0);

    const raycaster = new THREE.Raycaster();
    const mouse = new THREE.Vector2();

    const onMouseClick = (event) => {
      mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
      mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

      raycaster.setFromCamera(mouse, camera);
      const intersects = raycaster.intersectObjects(hitboxesRef.current, true);

      if (intersects.length > 0) {
        const hitbox = intersects[0].object;
        const intersectionPoint = intersects[0].point;
        const index = hitboxesRef.current.indexOf(hitbox);

        if (index !== -1 && mixersRef.current[index] && !mixersRef.current[index].isDead) {
          const { mixer, action } = mixersRef.current[index];
          
          // Play animation
          action.reset().play();
          mixersRef.current[index].isDead = true;

          // Play sound
          const hitAudio = new Audio(hitSounds[Math.floor(Math.random() * hitSounds.length)]);
          hitAudio.volume = volume;
          hitAudio.play();

          // Add skull icon
          if (!hitBanditsRef.current.has(index)) {
            const skullSprite = createSkullIcon(banditsRef.current[index].position);
            scene.add(skullSprite);
            skullIconsRef.current.push(skullSprite);
            hitBanditsRef.current.add(index);
            setRemainingBandits(prev => prev - 1);
          }

          // Trigger particles
          if (particleSystemRef.current) {
            particleSystemRef.current.triggerParticles(intersectionPoint);
          }
        }
      }
    };

    const createSkullIcon = (position) => {
      const spriteMap = new THREE.TextureLoader().load(skullIcon);
      const spriteMaterial = new THREE.SpriteMaterial({ 
        map: spriteMap,
        transparent: true,
        alphaTest: 0.5
      });
      const sprite = new THREE.Sprite(spriteMaterial);
      sprite.position.set(position.x, position.y + 3, position.z);
      sprite.scale.set(0.8, 0.8, 0.8);
      return sprite;
    };

    window.addEventListener('click', onMouseClick);

    const animate = () => {
      requestAnimationFrame(animate);
      const delta = clockRef.current.getDelta();

      // Update animations
      mixersRef.current.forEach(({ mixer, isDead }) => {
        if (mixer && isDead) mixer.update(delta);
      });

      // Update hitboxes
      banditsRef.current.forEach((bandit, index) => {
        hitboxesRef.current[index].position.copy(bandit.position);
        hitboxesRef.current[index].quaternion.copy(bandit.quaternion);
      });

      controls.update();
      renderer.render(scene, camera);
    };

    animate();

    return () => {
      window.removeEventListener('click', onMouseClick);
      mountRef.current.removeChild(renderer.domElement);
    };
  }, [volume]);

  const handleLeaveArea = () => {
    history.push('/looting');
  };

  return (
    <div ref={mountRef} className="wasteland-container">
      <ParticleSystem ref={particleSystemRef} scene={sceneRef.current} />
      {remainingBandits === 0 && (
        <button onClick={handleLeaveArea} className="leave-area-button">
          Leave area
        </button>
      )}
    </div>
  );
};

export default Wasteland;